import { reactive, ref, toRefs } from "vue";
import router from "@/router";
import { useRoute } from "vue-router";
export default {
  //接受父组件方法
  emits: ["addaddress"],
  setup(props, {
    emit
  }) {
    //获得传来的数据
    let route = useRoute();
    let num = ref(route.query.num);
    //输入的信息
    let data = reactive({
      name: "",
      phone: "",
      large: "",
      datail: ""
    });
    // 地址选择也
    const show = ref(false);
    const fieldValue = ref("");
    const cascaderValue = ref("");
    // 选项列表，children 代表子选项，支持多级嵌套
    const options = [{
      text: "广东省",
      value: "330000",
      children: [{
        text: "广州市",
        value: "330100",
        children: [{
          text: "从化区",
          value: "330100"
        }, {
          text: "天河区",
          value: "330101"
        }]
      }, {
        text: "深圳市",
        value: "330101",
        children: [{
          text: "宝安区",
          value: "330100"
        }, {
          text: "南山区",
          value: "330101"
        }]
      }]
    }, {
      text: "广西壮族自治区",
      value: "320000",
      children: [{
        text: "南宁市",
        value: "320100",
        children: [{
          text: "青秀区",
          value: "330100"
        }, {
          text: "武鸣区",
          value: "330101"
        }]
      }, {
        text: "梧州市",
        value: "320101",
        children: [{
          text: "岑溪镇",
          value: "330100"
        }]
      }]
    }];
    // 全部选项选择完毕后，会触发 finish 事件
    const onFinish = ({
      selectedOptions
    }) => {
      show.value = false;
      fieldValue.value = selectedOptions.map(option => option.text).join("/");
    };
    // 默认选项
    const checked = ref(false);

    //添加输入的地址信息
    let addlocal = () => {
      //有值的话就修改
      if (route.query.id) {
        //取出
        let change = JSON.parse(window.localStorage.getItem("address"));
        //查找
        let index = change.findIndex(item => {
          return item.id == route.query.id;
        });
        //删除,插入
        change.splice(index, 1, {
          id: parseInt(route.query.id),
          name: data.name,
          phone: data.phone,
          large: fieldValue.value,
          datail: data.datail,
          checked: checked.value
        });
        window.localStorage.setItem("address", JSON.stringify(change));
        router.go(-1);
        //没值的话就添加
      } else {
        //传输入的参数
        emit("addaddress", {
          id: 10,
          name: data.name,
          phone: data.phone,
          large: fieldValue.value,
          datail: data.datail,
          checked: checked.value
        });
        router.go(-1);
      }
    };
    // 如果有id值则执行
    if (route.query.id) {
      //在本地数据中查找出来
      let change = JSON.parse(window.localStorage.getItem("address"));
      //对应下标
      let index = change.findIndex(item => {
        return item.id == route.query.id;
      });
      data.name = change[index].name;
      data.phone = change[index].phone;
      data.datail = change[index].datail;
      fieldValue.value = change[index].large;
      checked.value = change[index].checked;
    }

    // 点击删除
    let del = () => {
      console.log("1");
      //取出
      let change = JSON.parse(window.localStorage.getItem("address"));
      //查找
      let index = change.findIndex(item => {
        return item.id == route.query.id;
      });
      //删除
      change.splice(index, 1);
      //保存进本地
      window.localStorage.setItem("address", JSON.stringify(change));
      router.go(-1);
    };
    return {
      ...toRefs(data),
      // 详细地址的参数
      show,
      options,
      onFinish,
      fieldValue,
      cascaderValue,
      //默认地址的参数
      checked,
      //输入的信息
      addlocal,
      //判断是否显示删除数据
      num,
      //点击删除
      del
    };
  }
};